<a name="process77"></a>

<p><br></p>
<p><span class="rvts17">框架符文：</span></p>
<p><span class="rvts21">框架符文包括能量型（</span><span class="rvts20">Power</span><span class="rvts21">）和神秘型（</span><span class="rvts20">Mystical</span><span class="rvts21">）两种，合成公式比较简单，只需将符文与框架结合，便可以获得。</span>
<br><span class="rvts21">然而，符文框架的来源有</span><span class="rvts20">3</span><span class="rvts21">种，第一种：少数</span><span class="rvts20"></span><span class="rvts21">高端</span><span class="rvts20">擭PC</span><span class="rvts21">出售；第二种：击杀比较重要的</span><span class="rvts20">NPC</span><span class="rvts21">或怪物后掉落；第三种开宝箱获得（需要较高的幸运值），因此符文框架相对来说是一种比较稀缺的物品。</span>
<br><span class="rvts21">另外，普通框架符文所对应的</span><span class="rvts20"></span><span class="rvts21">终极框架符文</span><span class="rvts20"></span><span class="rvts21">，属性加成类型是完全一致的，只是依据等级不同会略有强弱。</span>
<br><span class="rvts48"></span>
<br><span class="rvts29">符文框架类型：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="59" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图标</span></p>
</td>
<td width="97" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="158" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="837" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="59" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="56" height="55" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber533.jpg"></p>
</td>
<td width="97" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">能量符文框架</span></p>
</td>
<td width="158" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Rune Frame of Power</span></p>
</td>
<td width="837" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">能量型符文依据符文类型不同：</span>
<br><span class="rvts20">1. </span><span class="rvts21">为武器提供额外的基础属性加成（力量，智慧，灵巧），并转化武器伤害为符文的类型；</span>
<br><span class="rvts20">2. </span><span class="rvts21">为盔甲提供额外的基础属性加成（力量，智慧，灵巧），并提供额外的符文类型抗性；</span>
<br><span class="rvts20">3. </span><span class="rvts21">为护身符提供额外的基础属性加成（力量，智慧，灵巧），并提供额外的躲闪，生命值，精准，移动距离等。</span></p>
</td>
</tr>
<tr valign="top">
<td width="59" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="60" height="62" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber534.jpg"></p>
</td>
<td width="97" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">神秘符文框架</span></p>
</td>
<td width="158" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Mystical Rune Frame</span></p>
</td>
<td width="837" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">神秘型符文依据符文类型不同：</span>
<br><span class="rvts20">1. </span><span class="rvts21">为武器提供额外的战斗能力加成（武器能力），并转化武器伤害为符文的类型；</span>
<br><span class="rvts20">2. </span><span class="rvts21">为盔甲提供额外的战斗能力加成（战斗派系），并提供额外的符文类型抗性；</span>
<br><span class="rvts20">3. </span><span class="rvts21">为护身符提供额外的战斗能力加成（战斗派系），并提供额外的躲闪，生命值，精准，移动距离等。</span></p>
</td>
</tr>
</table>
